.toggle-button{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 24px;
    width: 30px;
    background: transparent;
    border: none;
    padding: 0;
     box-sizing: border-box;
}

.toggle-button:focus{
    outline: none;
}

.toggle-button_line{
    width: 30px;
    height: 1.5px;
    background: rgb(34, 32, 32);
}